$Mulish: ("Mulish","Regular"),
            ("Mulish","Medium"),
            ("Mulish","Bold");

            

@each $family,$weight in $Mulish {
    @font-face {
        font-family: #{$family}-#{$weight};
        font-weight: $weight;
        src: url(../fonts/#{$family}/#{$family}-#{$weight}.ttf);
    }
};