* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}


$white: #fff;
$red: #D61F00;
$blackBlue: #06163A;
$brokenWhite: #FAF9F8;
$lightBlue: #D7E5F0;
$body: #7A7A7A;
$title: #0A2647;
$disable: #E6E6E6;

body {
    background: $brokenWhite;

    &.lock {
        overflow: hidden;
    }
}

a {
    text-decoration: none;
}

ul {
    list-style: none;
}

.container {
    @include container();
    height: 100%;
}

.row {
    @include row();
}


.btn {
    padding: clamp(5px,0.7vw,25px) clamp(15px,calc(35vw/14.4),50px);
    background: $red;
    color: $white;
    font-family: "Mulish-Bold";
    border: none;
    font-size: clamp(8px,calc(16vw/14.4),24px);
    line-height: clamp(16px,clamp(24vw/14.4),32px);
}