.cards {
    padding: 50px 0;

    &__body {
        display: flex;
        justify-content: space-between;
        gap: 15px;
        flex-wrap: wrap;
    }

    &__card {
        flex: 1 1 20%;
        padding: clamp(15px,calc(30vw/14.4),40px) clamp(20px,calc(25vw/14.4),30px);
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        background-color: $lightBlue;
        transition: 0.3s;
        cursor: pointer;
        flex-wrap: wrap;
        
        
        @include media($md) {
            flex: 1 1 45%;
        }

        @include media($xs) {
            flex: 1 1 100%;
        }

        &:hover .cards__title,
        &:hover .cards__text,
        &:hover .cards__link{
            @include media-min($xs) {
                color: $white;
            }
        }
        &:hover {
            @include media-min($xs) {
                background-color: $title;
            }
        }
    }

    &__circle{ 
        width: clamp(30px,calc(64vw/14.4),75px);
        height: clamp(30px,calc(64vw/14.4),75px);
        background: $white;
        border-radius: 50%;
        margin-bottom: clamp(15px,calc(40vw/14.4),50px);
    }

    &__title {
        font-family: "Mulish-Bold";
        font-size: clamp(16px,calc(24vw/14.4),30px);
        line-height: clamp(20px,calc(32vw/14.4),36px);
        margin-bottom: clamp(5px,calc(20vw/14.4),25px);
        color: $title;
        transition: 0.3s;
    }

    &__text {
        font-family: "Mulish-Regular";
        font-size: clamp(12px,calc(16vw/14.4),20px);
        line-height: clamp(16px,calc(20vw/14.4),24px);
        margin-bottom: clamp(5px,calc(20vw/14.4),25px);
        color: $title;
        transition: 0.3s;
    }

    &__link {
        font-family: "Mulish-Bold";
        font-size: clamp(12px,calc(16vw/14.4),20px);
        line-height: clamp(16px,calc(20vw/14.4),24px);
        color: $red;
        transition: 0.3s;
    }

}