.header {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: clamp(30px,calc(80vw/14.4),100px);
    background: $white;
    display: flex;
    align-items: center;

    &__nav,
    &__list {
        display: flex;
        justify-content: space-between;
        align-items: center;
        height: 100%;
    }

    &__list {
        gap: clamp(20px,calc(50vw/14.4),70px);

        @include media($sm) {
            position: fixed;
            top: 0;
            right: 0;
            flex-direction: column;
            justify-content: space-evenly;
            align-items: start;
            padding: 10px 15px 10px 50px;
            height: 100%;
            background: $lightBlue;
            width: 0%;
            transition: all 600ms cubic-bezier(.62,.04,.3,1.56);
            transition-delay: 100ms;
            opacity: 0;

            &.active {
                width: 40%;
                opacity: 1;
            }
        }

        @include media($xs) {
            height: 100%;

            &.active {
                width: 100%;
            }
        }
    }
    
    &__link {
        font-family: "Mulish-Regular";
        font-size: clamp(12px,calc(16vw/14.4),24px);
        line-height: clamp(16px,calc(20vw/14.4),28px);
        color: $title;

        @include media($xs) {
            font-size: 36px;
            line-height: 48px;
        }
    }
    
    &__logo {
        
        & img {
            width: clamp(70px,80%,160px);
            height: clamp(40px,80%,100px);
            object-fit: contain;
        }
    }
    
    &__burger {
        width: 30px;
        height: 20px;
        position: relative;
        // margin: 50px auto;
        transform: rotate(0deg);
        transition: .5s ease-in-out;
        cursor: pointer;
        order: 3;
        transition: 0.5s;
        display: none;
        z-index: 999;
    
        @include media($sm) {
            display: inline-block;
        }

        &.open {
            transform: translateX(40px);
        }

        @include media(640px) {
            &.open {
                transform: translateX(0px);
            }
        }
    
        &.open .stick-1 {
            transform: rotate(135deg);
            top: 9px;
        }
        &.open .stick-2 {
            opacity: 0;
            left: -60px;
        }
        &.open .stick-3 {
            // bottom: unset;
            top: 9px;
            transform: rotate(-135deg);
        }
    
        & .stick {
            display: block;
            position: absolute;
            height: 2px;
            width: 100%;
            background: #d3531a;
            border-radius: 9px;
            opacity: 1;
            left: 0;
            transform: rotate(0deg);
            transition: .25s ease-in-out;
        }
    
        & .stick-1 {
            top: 0px;
        }
    
        & .stick-2 {
            top: 9px;
        }
    
        & .stick-3 {
            top: 18px;
            // bottom: 0;
        }
    }

    &__btn {
        order: 2;
    }



}