.start {
    height: 100vh;
    background: $blackBlue;
    padding-top: clamp(30px,calc(80vw/14.4),100px);

    &__body {
        width: 100%;
        height: 100%;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: start;
    }

    &__title {
        font-family: "Mulish-Bold";
        font-size: clamp(32px,calc(72vw/14.4),84px);
        line-height: clamp(36px,calc(80vw/14.4),90px);
        margin-bottom: clamp(5px,calc(20vw/14.4),25px);
        color: $white;
        max-width: 80%;

        @include media($xs) {
            max-width: 100%;
        }
    }

    &__text {
        font-family: "Mulish-Regular";
        font-size: clamp(12px,calc(16vw/14.4),20px);
        line-height: clamp(16px,calc(20vw/14.4),24px);
        margin-bottom: clamp(20px,calc(50vw/14.4),60px);
        color: $white;
        max-width: 60%;

        @include media($xs) {
            max-width: 100%;
        }
    }
}