@font-face {
  font-family: Mulish-Regular;
  font-weight: "Regular";
  src: url(../fonts/Mulish/Mulish-Regular.ttf);
}

@font-face {
  font-family: Mulish-Medium;
  font-weight: "Medium";
  src: url(../fonts/Mulish/Mulish-Medium.ttf);
}

@font-face {
  font-family: Mulish-Bold;
  font-weight: "Bold";
  src: url(../fonts/Mulish/Mulish-Bold.ttf);
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  background: #FAF9F8;
}

body.lock {
  overflow: hidden;
}

a {
  text-decoration: none;
}

ul {
  list-style: none;
}

.container {
  max-width: 1140px;
  width: 100%;
  margin-left: auto;
  margin-right: auto;
  padding-right: 15px;
  padding-left: 15px;
  height: 100%;
}

@media (max-width: 1140px) {
  .container {
    max-width: 992px;
  }
}

@media (max-width: 992px) {
  .container {
    max-width: 768px;
  }
}

@media (max-width: 768px) {
  .container {
    max-width: 576px;
  }
}

@media (max-width: 576px) {
  .container {
    max-width: 100%;
  }
}

.row {
  display: flex;
  flex-wrap: wrap;
  margin-left: -15px;
  margin-right: -15px;
}

.btn {
  padding: clamp(5px, 0.7vw, 25px) clamp(15px, calc(35vw/14.4), 50px);
  background: #D61F00;
  color: #fff;
  font-family: "Mulish-Bold";
  border: none;
  font-size: clamp(8px, calc(16vw/14.4), 24px);
  line-height: clamp(16px, clamp(24vw/14.4), 32px);
}

.header {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: clamp(30px, calc(80vw/14.4), 100px);
  background: #fff;
  display: flex;
  align-items: center;
}

.header__nav, .header__list {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 100%;
}

.header__list {
  gap: clamp(20px, calc(50vw/14.4), 70px);
}

@media (max-width: 768px) {
  .header__list {
    position: fixed;
    top: 0;
    right: 0;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: start;
    padding: 10px 15px 10px 50px;
    height: 100%;
    background: #D7E5F0;
    width: 0%;
    transition: all 600ms cubic-bezier(0.62, 0.04, 0.3, 1.56);
    transition-delay: 100ms;
    opacity: 0;
  }
  .header__list.active {
    width: 40%;
    opacity: 1;
  }
}

@media (max-width: 576px) {
  .header__list {
    height: 100%;
  }
  .header__list.active {
    width: 100%;
  }
}

.header__link {
  font-family: "Mulish-Regular";
  font-size: clamp(12px, calc(16vw/14.4), 24px);
  line-height: clamp(16px, calc(20vw/14.4), 28px);
  color: #0A2647;
}

@media (max-width: 576px) {
  .header__link {
    font-size: 36px;
    line-height: 48px;
  }
}

.header__logo img {
  width: clamp(70px, 80%, 160px);
  height: clamp(40px, 80%, 100px);
  object-fit: contain;
}

.header__burger {
  width: 30px;
  height: 20px;
  position: relative;
  transform: rotate(0deg);
  transition: .5s ease-in-out;
  cursor: pointer;
  order: 3;
  transition: 0.5s;
  display: none;
  z-index: 999;
}

@media (max-width: 768px) {
  .header__burger {
    display: inline-block;
  }
}

.header__burger.open {
  transform: translateX(40px);
}

@media (max-width: 640px) {
  .header__burger.open {
    transform: translateX(0px);
  }
}

.header__burger.open .stick-1 {
  transform: rotate(135deg);
  top: 9px;
}

.header__burger.open .stick-2 {
  opacity: 0;
  left: -60px;
}

.header__burger.open .stick-3 {
  top: 9px;
  transform: rotate(-135deg);
}

.header__burger .stick {
  display: block;
  position: absolute;
  height: 2px;
  width: 100%;
  background: #d3531a;
  border-radius: 9px;
  opacity: 1;
  left: 0;
  transform: rotate(0deg);
  transition: .25s ease-in-out;
}

.header__burger .stick-1 {
  top: 0px;
}

.header__burger .stick-2 {
  top: 9px;
}

.header__burger .stick-3 {
  top: 18px;
}

.header__btn {
  order: 2;
}

.start {
  height: 100vh;
  background: #06163A;
  padding-top: clamp(30px, calc(80vw/14.4), 100px);
}

.start__body {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: start;
}

.start__title {
  font-family: "Mulish-Bold";
  font-size: clamp(32px, calc(72vw/14.4), 84px);
  line-height: clamp(36px, calc(80vw/14.4), 90px);
  margin-bottom: clamp(5px, calc(20vw/14.4), 25px);
  color: #fff;
  max-width: 80%;
}

@media (max-width: 576px) {
  .start__title {
    max-width: 100%;
  }
}

.start__text {
  font-family: "Mulish-Regular";
  font-size: clamp(12px, calc(16vw/14.4), 20px);
  line-height: clamp(16px, calc(20vw/14.4), 24px);
  margin-bottom: clamp(20px, calc(50vw/14.4), 60px);
  color: #fff;
  max-width: 60%;
}

@media (max-width: 576px) {
  .start__text {
    max-width: 100%;
  }
}

.cards {
  padding: 50px 0;
}

.cards__body {
  display: flex;
  justify-content: space-between;
  gap: 15px;
  flex-wrap: wrap;
}

.cards__card {
  flex: 1 1 20%;
  padding: clamp(15px, calc(30vw/14.4), 40px) clamp(20px, calc(25vw/14.4), 30px);
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  background-color: #D7E5F0;
  transition: 0.3s;
  cursor: pointer;
  flex-wrap: wrap;
}

@media (max-width: 992px) {
  .cards__card {
    flex: 1 1 45%;
  }
}

@media (max-width: 576px) {
  .cards__card {
    flex: 1 1 100%;
  }
}

@media (min-width: 576px) {
  .cards__card:hover .cards__title,
  .cards__card:hover .cards__text,
  .cards__card:hover .cards__link {
    color: #fff;
  }
}

@media (min-width: 576px) {
  .cards__card:hover {
    background-color: #0A2647;
  }
}

.cards__circle {
  width: clamp(30px, calc(64vw/14.4), 75px);
  height: clamp(30px, calc(64vw/14.4), 75px);
  background: #fff;
  border-radius: 50%;
  margin-bottom: clamp(15px, calc(40vw/14.4), 50px);
}

.cards__title {
  font-family: "Mulish-Bold";
  font-size: clamp(16px, calc(24vw/14.4), 30px);
  line-height: clamp(20px, calc(32vw/14.4), 36px);
  margin-bottom: clamp(5px, calc(20vw/14.4), 25px);
  color: #0A2647;
  transition: 0.3s;
}

.cards__text {
  font-family: "Mulish-Regular";
  font-size: clamp(12px, calc(16vw/14.4), 20px);
  line-height: clamp(16px, calc(20vw/14.4), 24px);
  margin-bottom: clamp(5px, calc(20vw/14.4), 25px);
  color: #0A2647;
  transition: 0.3s;
}

.cards__link {
  font-family: "Mulish-Bold";
  font-size: clamp(12px, calc(16vw/14.4), 20px);
  line-height: clamp(16px, calc(20vw/14.4), 24px);
  color: #D61F00;
  transition: 0.3s;
}
